import React, { Fragment } from 'react';
import { Icon } from 'semantic-ui-react';

import './ProfilePage.scss';

const ProfilePage = ({ profile, errorMsg }) => {
	let sityResult,
		languagesResult,
		socialResult = null;

	if (profile) {
		const { city, languages, social } = profile;

		sityResult = <div>Город: {city}</div>;

		languagesResult = languages.map((l, idx) => {
			return <li key={idx}>{l}</li>;
		});

		languagesResult = (
			<div>
				Знание языков: <ul>{languagesResult}</ul>
			</div>
		);

		socialResult = social.map(({ label, link }, idx) => {
			return <Link key={idx} id={idx} link={link} label={label} />;
		});
	}

	const error = errorMsg ? <div>{errorMsg}</div> : null;

	return (
		<Fragment>
			<h2>ProfilePage</h2>
			{sityResult}
			{languagesResult}
			{socialResult}
			{error}
		</Fragment>
	);
};

export default ProfilePage;

function Link({ id, label, link }) {
	return (
		<li key={id}>
			{label} -{' '}
			<a href={link}>
				<Icon name="home" size="small" />
			</a>
		</li>
	);
}
